@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap");
:root {
  --red: #8e0404;
  --white: #fff;
  --black: rgb(0, 0, 30);
}

:root {
  --font-Sans: "Open Sans", sans-serif;
  --font-Poppins: "Poppins", sans-serif;
  --font-Normal: 400;
  --font-semiBold: 600;
  --font-Bold: 700;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

body {
  position: relative;
  font-family: var(--font-Poppins);
  font-size: 16px;
  overflow-x: hidden;
}
body.no-scroll {
  overflow: hidden;
}

img {
  height: auto;
  max-width: 100%;
}

ul,
ol {
  list-style: none;
}

button,
input {
  font-family: var(--fontNormal);
}

a,
a:visited,
a:active {
  text-decoration: none;
}
a:focus,
a:visited:focus,
a:active:focus {
  outline: none;
}
a:focus-visible,
a:visited:focus-visible,
a:active:focus-visible {
  outline: 2px dashed var(--red);
  outline-offset: 3px;
}

.container {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s ease-in-out;
}
@media screen and (max-width: 768px) {
  .overlay.overlay-active {
    visibility: visible;
    opacity: 1;
  }
}

h1,
h2,
h3,
h4,
p {
  font-family: var(--font-Sans);
}

.heading, .heading--xxl {
  color: var(--black);
  font-weight: 400;
}
.heading__name {
  color: var(--red);
}
.heading--xxl {
  font-size: clamp(34px, 6vw, 53px);
  max-width: 430px;
  font-weight: 700;
  margin-bottom: 20px;
}
.heading--xl {
  position: relative;
  font-size: clamp(20px, 4vw, 30px);
  font-weight: 700;
  text-align: center;
  color: var(--red);
  margin-bottom: 39px;
}
.heading--xl::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -5px;
  height: 5px;
  width: 64px;
  margin: 0 auto;
  background-color: var(--red);
}
.heading--l {
  font-size: 24px;
  font-weight: 600;
  color: var(--red);
  margin-bottom: 29px;
}

.paragraph, .paragraph--l {
  line-height: 1.2;
}
.paragraph__name {
  color: var(--black);
  font-weight: 700;
}
.paragraph--l {
  font-size: clamp(23px, 3vw, 27px);
  margin-bottom: 26px;
}
.paragraph--about {
  font-size: 15px;
  margin-bottom: 17px;
  line-height: 24px;
}

.learnMore__text {
  color: var(--red);
  font-weight: 700;
  font-size: 17px;
  letter-spacing: 1px;
  margin-right: 18px;
  transition: margin-right 0.3s ease-in-out;
}
.learnMore__icon svg path {
  fill: var(--red);
}

.btn, .btn--start, .btn--blue, .btn--menu {
  position: relative;
  outline: none;
  border: none;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 300ms, border 300ms ease-in;
}
.btn:focus, .btn--start:focus, .btn--blue:focus, .btn--menu:focus {
  outline: none;
}
.btn:focus-visible, .btn--start:focus-visible, .btn--blue:focus-visible, .btn--menu:focus-visible {
  outline: 2px dashed var(--red);
  outline-offset: 3px;
}
.btn--menu {
  display: flex;
  justify-self: center;
  align-items: center;
  background-color: transparent;
  z-index: 10;
}
@media screen and (min-width: 769px) {
  .btn--menu {
    display: none;
  }
}
.btn--blue {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--red);
  border: 2px solid var(--red);
  border-radius: 10px;
  color: var(--white);
  box-shadow: 0px 10px 15px -7px var(--black);
}
.btn--blue:hover {
  background-color: var(--white);
  color: var(--red);
}
.btn--start {
  width: 180px;
  height: 48px;
}
.btn--more:hover .learnMore__text {
  margin-right: 26px;
}

.icon__skills {
  color: var(--red);
  font-size: 35px;
  margin-right: 20px;
}

.header {
  position: absolute;
  left: 0;
  width: 100%;
  padding: 24px 18px 0;
  z-index: 999;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 51px;
}
@media screen and (max-width: 768px) {
  .header__container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 24px 18px 14px;
    height: 70px;
    transition: all 0.3s;
  }
  .header__container.header--scroll {
    background-color: var(--black);
    padding: 14px 18px 14px;
    transition: all 0.3s;
  }
}
.header__logo {
  z-index: 1;
  font-size: 24px;
  color: var(--black);
}
@media screen and (max-width: 768px) {
  .header__logo.active-color {
    color: var(--white);
  }
}
.header__logo--letter {
  font-size: 30px;
  color: var(--red);
}
@media screen and (max-width: 768px) {
  .header__menu {
    position: fixed;
    width: 100%;
    top: 0px;
    right: -1000px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--black);
    transition: all 500ms ease-in;
  }
  .header__menu.is-active {
    right: 0px;
  }
}
.header__list {
  display: flex;
  align-items: center;
}
@media screen and (min-width: 769px) {
  .header__item:not(:last-child) {
    margin-right: 28px;
  }
}
@media screen and (max-width: 768px) {
  .header__list {
    display: grid;
    grid-template-columns: max-content;
    grid-template-rows: repeat(5, 3.75rem);
    align-items: center;
    justify-items: center;
  }
}
.header__link {
  position: relative;
  font-size: 17px;
  font-weight: 600;
  color: var(--white);
  text-transform: uppercase;
}
.header__link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  height: 4px;
  width: 100%;
  background-color: var(--red);
  visibility: hidden;
  opacity: 0;
  transform: scale(0);
}
@media screen and (min-width: 769px) {
  .header__link {
    color: var(--black);
    font-size: 15px;
  }
  .header__link:hover {
    color: var(--red);
  }
  .header__link:hover::before {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
    transition: all 350ms ease-in;
  }
}

.hero {
  position: relative;
  padding: 0 18px;
}
.hero__container {
  display: flex;
  flex-direction: column;
  padding: 166px 0 140px 0;
}
@media screen and (min-width: 769px) {
  .hero__container {
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }
}
.hero__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
@media screen and (min-width: 769px) {
  .hero__text {
    align-items: flex-start;
    flex: 1 0 50%;
    text-align: left;
  }
}

.about {
  padding: 0 18px 170px;
}
.about__container {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  align-items: center;
}
@media screen and (min-width: 769px) {
  .about__container {
    grid-template-columns: 380px 1fr;
  }
}
.about__img {
  width: 286px;
  height: 270px;
}

.skills {
  position: relative;
  padding: 0 18px;
}
.skills__container {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  .skills__container {
    flex-direction: row;
    gap: 20px;
  }
}
.skills__tech, .skills__tools {
  display: flex;
  flex-direction: column;
  justify-self: center;
  width: 100%;
}
.skills__names {
  display: grid;
  grid-template-columns: 70px 1fr;
  align-items: center;
  width: 100%;
  font-size: 16px;
  font-weight: 600;
  text-align: left;
  padding: 10px 28px;
  margin-bottom: 15px;
  box-shadow: 0 0 6px 2px rgba(48, 48, 48, 0.1);
}
.skills__names:hover {
  transform: translateY(-8px);
  transition: transform 0.3s ease-in-out;
}